.element {
	margin: 0;
	padding: 0;
	display: grid;
	gap: 20px 17px;
  grid-template-columns: repeat(3, minmax(min-content, 1fr));
  justify-content: center;
}

@media screen and (max-width: 900px) {
  .element {
		grid-template-columns: repeat(1, minmax(min-content, 1fr));
		gap: 20px;
  }
}