.profile__name {
	margin: 0;
	max-width: 294px;
	padding-bottom: 8px;
	color: #fff;
	font-weight: 500;
	font-size: 42px;
	line-height: 48px;
	text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 1100px) {
  .profile__name {
    padding-bottom: 6px;
		max-width: 189px;
		font-size: 27px;
		line-height: 33px;
		text-align: center;
  }
}
