.profile {
  margin: 0 auto;
  padding-top: 25px;
  padding-bottom: 46px;
  max-width: 880px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1100px) {
  .profile {
    margin: 0 auto;
    padding-bottom: 36px;
    width: 282px;
    flex-direction: column;
  }
}
